@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@500&family=Noto+Sans+JP&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* first page */

.form {
    width: 40%;
    margin: 8rem auto;
    border: 10px solid blue;
    display: flex;
}
.form1 {
    flex-direction: column;
}
.wrapper {
    display: flex;
    justify-content: space-evenly;
    margin-left: 2rem;
    margin-top: 2rem;
    color: rgb(26, 138, 243);
}
.title {
    font-size: 3rem;
}
#glass {
    background-image: url(/waterapp.github.io/static/media/flat-design-water-glass-icon-cartoon-style-isolated-white-background-soda-glass-vector-illustration-liquid-water-business-157410619.59fce108.jpg);
    background-size: cover;
    background-position: center;
}
.glass {
    width: 30%;
    height: 10rem;
    align-items: center;
} 
.wrap {
    display: flex;
    justify-content: space-between;
    margin: 1rem;
}
#design {
    display: flex;
    align-items: flex-end;
    color: rgb(26, 138, 243);
    font-family: 'Dancing Script', cursive;
}
#but {
    background-color: rgb(26, 138, 243);
    border-color: rgb(26, 138, 243);
    color: white;
    text-transform: uppercase;
    font-weight: bold;
    height: 3rem;
}
.start {
    width: 20%;
}

/* second page */
.form2 {
    justify-content: space-between;
}
.wrapper2 {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    width: 60%;
    margin: 1rem;
}
#field {
    width: 80%;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    margin: 1rem;
}
#ques {
    margin-bottom: 1rem;
}
#btn {
    display: flex;
    align-items: flex-end;
}
.radio_btns {
    display: flex;
}
.next, .submit {
    width: 5rem;
    margin-bottom: 2rem;
    margin-right: 2rem;
} 
.next {
    display: none;
}

/* main page */
.appl {
    display: flex;
    width: 70%;
    margin: 2rem auto;
    flex-flow: row wrap;
    border: 5px solid blue;
}

/*-----------responsive-----------*/

/* Mobile version (<600px) */
@media only screen and (max-width: 599px) {
    .form1, .form2 {
        width: 80%;
    }
    .appl {
        width: 90%;
    }
}

/* Tablet version (portrait) >600px */
@media only screen and (min-width: 600px) {
    .form1, .form2 {
        width: 70%;
    }
}

/* Tablet version (landscape) >900px */
@media only screen and (min-width: 900px) {
    .form1, .form2 {
        width: 50%;
    }
}

/* Desktop version (>1200px) */
@media only screen and (min-width: 1200px) {
    .form1, .form2 {
        width: 40%;
    }
}
.main {
    width: 70%;
    height: 28rem;
    border-right: 1px solid blue;
}
#glass {
    background-image: url(/waterapp.github.io/static/media/flat-design-water-glass-icon-cartoon-style-isolated-white-background-soda-glass-vector-illustration-liquid-water-business-157410619.59fce108.jpg);
    background-size: cover;
    background-position: center;
}
.glass {
    width: 40%;
    margin: 3rem auto;
    height: 15rem;
}
#measure {
    display: flex;
    flex-direction: column;
    align-items: center;
}
#counter {
    font-size: 2rem; 
    margin-bottom: 1rem;
}
#line {
    height: 1rem;
    background-color: rgb(0, 225, 255);
    width: 70%;
}

/* --------------Responsive design-------------- */

/* Mobile version (<600px) */
@media only screen and (max-width: 599px) {
    .main {
        height: 18rem;
    }
    .glass {
        width: 40%;
        margin: 1rem auto;
        height: 10rem;
    }
    #counter {
        font-size: 1.5rem;
    }
}

/* Tablet version (portrait) >600px */
@media only screen and (min-width: 600px) {
    .main {
        height: 22rem;
    }
    .glass {
        height: 12rem;
        margin: 2rem auto;
    }
    #counter {
        font-size: 1.5rem;
    }
}

/* Tablet version (landscape) >900px */
@media only screen and (min-width: 900px) {
    .main {
        height: 24rem;
    }
    .glass {
        height: 14rem;
        margin: 2rem auto;
    }
    #counter {
        font-size: 2rem;
    }
}

/* Desktop version (>1200px) */
@media only screen and (min-width: 1200px) {
    .main {
        height: 28rem;
    }
    .glass {
        height: 15rem;
        margin: 3rem auto;
    }
}
.ml_scroll {
    width: 14%;
    height: 28rem;
    border-right: 1px solid blue;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.wrapper {
    display: flex;
    flex-direction: column;
    width: 80%;
    margin: 0 auto;
    overflow: hidden;
}
.slide {
    width: 90%;
    margin: 0 auto;
    height: 3rem;
    display: none;
}
.active {
    text-align: center;
    font-size: 1.5rem;
    display: block;
}
#up, #down {
    background-color: rgb(100, 223, 251, 0.6);
    border-color: rgb(100, 223, 251, 0.1);
    width: 40%;
    margin: 2rem auto;
}

/* --------------Responsive design-------------- */

/* Mobile version (<600px) */
@media only screen and (max-width: 599px) {
    .ml_scroll {
        height: 18rem;
        overflow: hidden;
    }
}

/* Tablet version (portrait) >600px */
@media only screen and (min-width: 600px) {
    .ml_scroll {
        height: 22rem;
        overflow: hidden;
    }
}

/* Tablet version (landscape) >900px */
@media only screen and (min-width: 900px) {
    .ml_scroll {
        height: 24rem;
    }
}

/* Desktop version (>1200px) */
@media only screen and (min-width: 1200px) {
    .ml_scroll {
        height: 28rem;
    }
}
.type_scroll {
    width: 14%;
    height: 28rem;
    display: flex;
    align-items: center;
    justify-content: center;
}
.slider {
    width: 90%;
    margin: 0 auto;
    height: 12rem;
    border: 1px solid rgb(100, 223, 251, 0.5);
    overflow: scroll;
    overflow-x: hidden;
    overflow-y: auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    color: rgb(100, 223, 251);
    font-size: 1.5rem;
    scrollbar-color: dark;
}


/* --------------Responsive design-------------- */

/* Mobile version (<600px) */
@media only screen and (max-width: 599px) {
    .type_scroll {
        height: 18rem;
    }
}

/* Tablet version (portrait) >600px */
@media only screen and (min-width: 600px) {
    .type_scroll {
        height: 22rem;
    }
}

/* Tablet version (landscape) >900px */
@media only screen and (min-width: 900px) {
    .type_scroll {
        height: 24rem;
    }
}

/* Desktop version (>1200px) */
@media only screen and (min-width: 1200px) {
    .type_scroll {
        height: 28rem;
    }
}
.stat {
    width: 100%;
    border-top: 1px solid blue;
    height: 5rem;
    border-bottom: 1px solid blue;
    padding-bottom: 3rem;
}
.app {
    display: flex;
    width: 100%;
}
